import {required} from "vuelidate/lib/validators";

export default {
  layout: 'AdminTemplate',
  name: "users"
  ,
  data() {
    return {
      cvsSavingProgress: false,
      file1: null,
      checkView: false,
      cvs_data:null,
      selected: null,
      options: ['list', 'of', 'options'],
      header_title: '',
      overlay_show: false ,
      tabVisible: 0,
      pageIndex: 1,
      nData: { id: 0 , subject: "" , active: true , uniId: null  },
      cvsData: { id: 0 , active: true , uniId: null  , selectedFile: null },
      active_options:[
        { value: 2, text: 'All' },
        { value: 0, text: 'Disable' },
        { value: 1, text: 'Enable' },
      ],
      universities_options:[ ],
      searchData: {
        pageIndex: 0,
        pageCount: 0,
        perPage: 10,
        id: 0,
        subject: "",
        uniId: null,
        active: 2 // 2=all , 0= disable , 1= enable
      },
      t_fields:[
        {
          key: 'subject',
          label: 'Subject',
          sortable: true
        },
        {
          key: 'code',
          label: 'Code',
          sortable: true
        },
        {
          key: 'University',
          label: 'University',
          sortable: true
        },
        {
          key: 'state',
          label: 'Status',
          sortable: false,

        },
        {
          key: 'actions',
          label: 'Edit',
          sortable: false
        }
        ,
        {
          key: 'action_del',
          label: '',
          sortable: false
        },
      ],
      t_items: []
    }
  }
  ,
  mounted()
  {

    this.searchToggle()
    this.search()
    this.init()
  },

  validations: {
    nData: {
      subject: {
        required,
      }
    },
    cvsData: {
      uniId: {
        required,
      },
      selectedFile: {
        required,
      },
    },

  },

  methods:
  {

    init(){
      this.$axios.post('/definitions/universityList', null).then((res) => {
        var rows = res.data
        this.universities_options = []
        for (let i = 0; i < res.data.length ; i++) {
          var row = rows[i]
          this.universities_options.push({value: row.id , text: row.name })
        }
      }).catch(error => {
        console.log(error)
      });
    },
    showModal() {
      this.$refs['edit-modal'].show()
    },
    hideModal(id) {
      this.$refs[id].hide()
    },

    addNewToggle()
    {
      this.header_title = this.$t('Add_new_lesson')
      this.add_visible = true;
      this.nData.id = 0
      this.nData.subject = ""
      this.nData.active = true
      this.$refs['edit-modal'].show()
    },
    addFromCVS(){
      this.header_title = this.$t('Add_new_lesson')
      this.add_visible = true;
      this.nData.id = 0
      this.nData.subject = ""
      this.nData.active = true
      this.$refs['add-modal-cvs'].show()
    },
    searchToggle()
    {
      this.header_title = this.$t('search')
      this.add_visible = false;
    },
    makeToast(variant = 'success', header = 'Message', msg = '')
    {
      this.$bvToast.toast(this.$t(msg), {
          title: header,
          variant: variant,
          solid: true
        })
     },

    search(index)
    {
      if(index > 0)
        this.searchData.pageIndex = index
      else
        this.searchData.pageIndex = 0

      this.overlay_show = true;
      this.$axios.post('/definitions/lessonList', this.searchData).then((res) => {
        this.t_items = res.data.data
        this.searchData.pageCount = res.data.count
        this.overlay_show = false;
      }).catch(error => {
        this.overlay_show = false;
        this.makeToast("danger" , "Error" , "search_error" )
        console.log(error)
      });
    }
    ,
    cancel(){
      this.nData.id = 0
      this.hideModal('edit-modal')
    },
    closeCVS(){
      this.cvsData.cvs = null
      this.cvsData.uniId = null
      this.cvsData.id = 0
      this.cvsData.active = true
      this.hideModal('add-modal-cvs')
    },
    prepare_edit(item)
    {
      this.header_title = this.$t("edit_lesson")
      this.nData.id = item.id
      this.nData.subject = item.subject
      this.nData.active = item.state !== "Disable"
      this.nData.uniId = item.UniversityId
      this.showModal()

    },
    saveEdit()
    {
      this.$v.$reset()
      this.$v.nData.$touch()
      if (this.$v.nData.$invalid) {
        console.log("vuelidate error")
      } else {

        if (this.nData.id > 0) {
          this.$axios.post('definitions/editLesson', this.nData).then((res) => {
            this.hideModal("edit-modal")
            this.nData.active = true
            this.overlay_show = false;
            this.search();
          }).catch(error => {
            this.overlay_show = false;
            this.makeToast("danger", "Error", "save_error")
            console.log(error)
          });
        }
        else
        {
          this.$axios.post('definitions/createLesson', this.nData).then((res) => {
            //this.t_items = res.data
            this.nData.subject = ""
            this.nData.active = true
            this.hideModal()
            this.search();

          }).catch(error => {
            this.overlay_show = false;
            this.makeToast("danger", "Error", error)
            console.log(error)
          });
        }
        this.nData.id = 0
      }


    },
    deleteLesson(item) {

      this.$bvModal.msgBoxConfirm(this.$t('delete_confirm'), {
        title: 'Confirm',
        size: 'sm',
        buttonSize: 'sm',
        okVariant: 'danger',
        okTitle: 'YES',
        cancelTitle: 'NO',
        headerClass: 'p-2 border-bottom-0',
        footerClass: 'p-2 border-top-0',
        hideHeaderClose: false,
        centered: true
      }) .then(value => {
        this.delComfirm = value

        if (value === true) {
          if (this.id === item.id)
            this.id = 0

          this.$axios.post('/definitions/delete-lesson', {'lessonId': item.id}).then(() => {
            this.search(this.pageIndex)
          } ).catch(error => {
            console.log(error)
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },

    saveCVSEdit()
    {
      this.$v.$reset()
      this.$v.cvsData.$touch()
      if (this.$v.cvsData.$invalid) {
        console.log("vuelidate error")
      } else {
        this.busy = true
        var formData = new FormData();
        if(this.cvsData.selectedFile!==null)
          formData.append("file", this.cvsData.selectedFile, this.cvsData.selectedFile.name)

        formData.append('id', this.cvsData.id)
        formData.append('active', this.cvsData.active)
        formData.append('uniId', this.cvsData.uniId)
        this.cvsSavingProgress = true
         this.$axios.post('definitions/createLessonBulk', formData).then((res) => {
            this.search();
            this.closeCVS()
          }).catch(error => {
           this.cvsSavingProgress = false
            this.overlay_show = false;
            this.makeToast("danger", "Error", error)
            console.log(error)

          }).finally(m => {
           this.cvsSavingProgress = false
         });
        }
        this.cvsData.id = 0


    }

  }

}
